@import url(https://fonts.googleapis.com/css?family=Overpass);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.center {
  margin: auto;
  width: 25%;
  padding: 10px;
}

.my-modalw {
  width: 90vw;
  max-width: 90vw;
}

.my-modalh {
  height: 45vw;
  white-space: pre-line;
  border-radius: 20px;
}

.notesTable tr {
  cursor: pointer;
}

.btn-close {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 50px;
  height: 50px;
  font-size: 0;
  opacity: 0.3;
}
.btn-close:hover {
  opacity: 1;
}

.btn-close:before,
.btn-close:after {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 5px;
  height: 20px;
  background-color: #000000;
  -webkit-transform: rotate(45deg) translate(-50%, -50%);
          transform: rotate(45deg) translate(-50%, -50%);
  -webkit-transform-origin: top left;
          transform-origin: top left;
  content: "";
}
.btn-close:after {
  -webkit-transform: rotate(-45deg) translate(-50%, -50%);
          transform: rotate(-45deg) translate(-50%, -50%);
}
.btn-close:before,
.btn-close:after {
  width: 2px;
}

.btn-close:before,
.btn-close:after {
  height: 25px;
}

.search {
  display: flex;
  align-items: center;
  background-color: rgb(233, 233, 233);
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 1.5em;
}

.search input {
  border: none;
  background-color: rgb(233, 233, 233);
  width: 100%;
}

.search input:focus {
  outline: none;
}

*{box-sizing:border-box;font-family:"Overpass",sans-serif}.tags-input{display:flex;align-items:flex-start;flex-wrap:wrap;min-height:48px;padding:0 8px;border:1px solid #d6d8da;border-radius:6px}.tags-input:focus-within{border:1px solid #0052cc}.tags-input input{flex:1 1;border:none;height:46px;font-size:14px;padding:4px 0 0 0}.tags-input input:focus{outline:transparent}#tags{display:flex;flex-wrap:wrap;padding:0;margin:8px 0 0 0}.tag{width:auto;height:32px;display:flex;align-items:center;justify-content:center;color:#fff;padding:0 8px;font-size:14px;list-style:none;border-radius:6px;margin:0 8px 8px 0;background:#0052cc}.tag .tag-title{margin-top:3px}.tag .tag-close-icon{display:block;width:16px;height:16px;line-height:16px;text-align:center;font-size:14px;margin-left:8px;color:#0052cc;border-radius:50%;background:#fff;cursor:pointer}@media screen and (max-width: 567px){.tags-input{width:calc(100vw - 32px)}}
